button {
  position: relative;
  display: inline-block;
  margin: 15px;
  padding: 15px 30px;
  text-align: center;
  font-size: 18px;
  letter-spacing: 1px;
  text-decoration: none;
  color: #330000;
  background: transparent;
  cursor: pointer;
  transition: ease-out 0.5s;
  border: 4px solid #330000;
  border-radius: 10px;
  box-shadow: inset 0 0 0 0 #725AC1;
}

button:hover {
  color: white;
  box-shadow: inset 0 -100px 0 0 #e60000;
}

button:active {
  transform: scale(0.9);
}

